/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useRef } from "react";
import { ReactSVG } from "react-svg";
import { useStore } from "zustand";
import shallow from "zustand/shallow";
import { EditorLayout } from "../../shared/components/EditorLayout";
import { pick } from "../../shared/utils/pick";
import { AddContainer } from "./components/AddContainer";
import { AddObject } from "./components/AddObject";
import { useEditDeploymentLogic } from "./logic/useEditDeploymentLogic";
import { createDeploymentStore } from "./store/deploymentStore";
import { useDrag } from "../../shared/hooks/useDrag";
import {
  ContainerObjectType,
  Deployment,
  findObject,
  LineType,
  RelationType,
} from "../../core/entities/Deployment";
import { useDebounceCallback } from "@react-hook/debounce";
import { ColorPicker } from "./components/ColorPicker";
import { createUndoStore } from "../../shared/store/undo";

export function DeploymentEditor() {
  const deploymentStore = useRef(createDeploymentStore()).current;
  const undoStore = useRef(createUndoStore<Deployment>()).current;
  const { allowRedo, allowUndo } = useStore(
    undoStore,
    (state) => ({
      allowUndo: state.undoIndex < state.queue.length - 1,
      allowRedo: state.undoIndex !== 0,
    }),
    shallow
  );
  const {
    handleInit,
    handleDiagramChange,
    handleAddContainer,
    handleAddObject,
    handleObjectSelect,
    handleDrop,
    handleObjectNameChange,
    handleDelete,
    handleToggleAllowDragRelation,
    handleDeleteRelation,
    handleSelectObjectBgColor,
    handleRelationChange,
    handleUndo,
    handleRedo,
    handleLineTypeChange,
  } = useEditDeploymentLogic([deploymentStore, undoStore]);
  const {
    currentObjectId,
    svgUrl,
    deployment,
    allowDragRelation,
    uml,
    pngUrl,
  } = useStore(
    deploymentStore,
    (state) =>
      pick(state, [
        "currentObjectId",
        "deployment",
        "svgUrl",
        "allowDragRelation",
        "uml",
        "pngUrl",
      ]),
    shallow
  );
  const currentObject = useStore(
    deploymentStore,
    (state) =>
      !state.deployment?.root
        ? undefined
        : findObject(state.deployment?.root, state.currentObjectId),
    shallow
  );
  const relations = deployment?.relations?.[currentObjectId] ?? [];
  const isRoot = currentObject?.type === ContainerObjectType.diagram;

  const dragElementRef = useRef<HTMLDivElement>();

  const boundHandleDiagramChange = useDebounceCallback(
    handleDiagramChange,
    600
  );

  useDrag("deployment-diagram", handleDrop);

  useEffect(() => {
    handleInit();
  }, []);

  useEffect(() => {
    boundHandleDiagramChange();
  }, [deployment]);

  return (
    <EditorLayout
      currentDiagram="deployment"
      uml={uml}
      pngUrl={pngUrl}
      svgUrl={svgUrl}
      diagram={
        <div>
          <div ref={dragElementRef} />
          <div
            className="deployment"
            id="deployment-diagram"
            style={{
              touchAction: "none",
            }}
            onClick={(e: any) => {
              const objectId = e.target?.attributes?.objectId?.value;
              if (objectId) {
                handleObjectSelect(objectId);
              }
            }}
          >
            {svgUrl && (
              <div>
                <ReactSVG src={svgUrl} />
              </div>
            )}
          </div>
        </div>
      }
      operation={
        <div>
          <div className="pb-8">
            <h3 className="pb-2 font-bold"></h3>
            <div className="space-x-1">
              <button
                className="btn btn-xs btn-outline"
                disabled={!allowUndo}
                onClick={handleUndo}
              >
                
              </button>
              <button
                className="btn btn-xs btn-outline"
                disabled={!allowRedo}
                onClick={handleRedo}
              >
                
              </button>
            </div>
          </div>
          <div className="pb-8">
            <h3 className="pb-2 font-bold"></h3>
            <div className="flex items-center">
              <input
                type="checkbox"
                className="toggle"
                checked={allowDragRelation ?? false}
                onChange={(e) =>
                  handleToggleAllowDragRelation(e.target.checked)
                }
              />
              <div className="text-gray-400 ml-4 text-xs">
                {allowDragRelation
                  ? ""
                  : ""}
              </div>
            </div>
          </div>
          <div className="pb-8">
            <h3 className="pb-2 font-bold"></h3>
            <div className="flex items-center">
              <div className="form-control">
                <label className="input-group input-group-xs">
                  <span></span>
                  <select
                    value={deployment?.linetype}
                    className="select select-bordered select-xs"
                    onChange={(e) => {
                      handleLineTypeChange(e.target.value as LineType);
                    }}
                  >
                    <option value={"default" as LineType}>Default</option>
                    <option value={"ortho" as LineType}>Ortho</option>
                    <option value={"polyline" as LineType}>Polyline</option>
                  </select>
                </label>
              </div>
            </div>
          </div>
          <div className="pb-8">
            <h3 className="pb-2 font-bold"></h3>
            <div>
              <div>
                {currentObject && (
                  <input
                    type="text"
                    className="input input-bordered w-full input-xs"
                    value={currentObject?.name}
                    onChange={(e) =>
                      handleObjectNameChange(currentObjectId, e.target.value)
                    }
                  />
                )}
              </div>
            </div>
          </div>
          {!isRoot && (
            <div className="pb-8">
              <h3 className="pb-2 font-bold"></h3>
              <div>
                <div className="flex">
                  <div className="flex text-xs items-center mr-8">
                    <button className="btn btn-xs btn-outline">
                      <span className="mr-4"></span>
                      <ColorPicker
                        color={currentObject?.bgColor || "#e5e7eb"}
                        onChange={(color) =>
                          handleSelectObjectBgColor(currentObjectId, color)
                        }
                      />
                    </button>
                  </div>
                </div>
              </div>
            </div>
          )}
          <div className="pb-8">
            <h3 className="pb-2 font-bold"></h3>
            <div>
              <AddContainer
                onClick={(type) =>
                  handleAddContainer(deployment?.root?.id, type)
                }
              />
            </div>
          </div>
          <div className="pb-8">
            <h3 className="pb-2 font-bold"></h3>
            <div>
              <AddObject
                onClick={(type) => handleAddObject(deployment?.root?.id, type)}
              />
            </div>
          </div>
          {relations?.length > 0 && (
            <div className="pb-8">
              <h3 className="pb-2 font-bold"></h3>
              <div className="-mb-5">
                {relations.map((item, idx) => {
                  const to = findObject(deployment.root, item.to);
                  return (
                    <div className="pb-5 pt-2" key={idx}>
                      <div>
                        <div className="form-control pb-2">
                          <label className="input-group input-group-xs">
                            <span></span>
                            <input
                              type="text"
                              className="input input-bordered input-xs"
                              disabled
                              value={to?.name}
                            />
                          </label>
                        </div>
                        <div className="flex space-x-1 pb-2">
                          <button className="btn btn-xs btn-outline">
                            <span className="mr-4"></span>
                            <ColorPicker
                              color={item.linkColor || "#000000"}
                              onChange={(color) => {
                                handleRelationChange(
                                  currentObjectId,
                                  item.id,
                                  "linkColor",
                                  color
                                );
                              }}
                            />
                          </button>
                          <button className="btn btn-xs btn-outline">
                            <span className="mr-4"></span>
                            <ColorPicker
                              color={item.descColor || "#000000"}
                              onChange={(color) => {
                                handleRelationChange(
                                  currentObjectId,
                                  item.id,
                                  "descColor",
                                  color
                                );
                              }}
                            />
                          </button>
                        </div>
                        <div className="form-control pb-2">
                          <label className="input-group input-group-xs">
                            <span></span>
                            <input
                              type="text"
                              className="input input-bordered input-xs"
                              value={item.name}
                              onChange={(e) =>
                                handleRelationChange(
                                  currentObjectId,
                                  item.id,
                                  "name",
                                  e.target.value
                                )
                              }
                            />
                          </label>
                        </div>
                        <div className="form-control pb-2">
                          <label className="input-group input-group-xs">
                            <span></span>
                            <select
                              value={item.type}
                              className="select select-bordered select-xs"
                              onChange={(e) => {
                                handleRelationChange(
                                  currentObjectId,
                                  item.id,
                                  "type",
                                  e.target.value as RelationType
                                );
                              }}
                            >
                              <option value={RelationType.dependency}>
                                
                              </option>
                              <option value={RelationType.association}>
                                
                              </option>
                              <option value={RelationType.composition}>
                                
                              </option>
                              <option value={RelationType.aggregation}>
                                
                              </option>
                              <option value={RelationType.realize}></option>
                              <option value={RelationType.generalization}>
                                
                              </option>
                            </select>
                          </label>
                        </div>
                        <div className="flex pb-2">
                          <div className="form-control">
                            <label className="input-group input-group-xs">
                              <span></span>
                              <select
                                value={item.linkDirection}
                                className="select select-bordered select-xs"
                                onChange={(e) => {
                                  handleRelationChange(
                                    currentObjectId,
                                    item.id,
                                    "linkDirection",
                                    (e.target.value === "-"
                                      ? ""
                                      : e.target.value) as any
                                  );
                                }}
                              >
                                <option value={"up"}></option>
                                <option value={"down"}></option>
                                <option value={"left"}></option>
                                <option value={"right"}></option>
                                <option value={"-"}></option>
                              </select>
                            </label>
                          </div>
                          <button
                            onClick={() =>
                              handleDeleteRelation(item.origin, item.to)
                            }
                            className="btn btn-outline btn-error btn-xs ml-1"
                          >
                            
                            <svg
                              xmlns="http://www.w3.org/2000/svg"
                              className="h-4 w-4"
                              fill="none"
                              viewBox="0 0 24 24"
                              stroke="currentColor"
                            >
                              <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth="2"
                                d="M6 18L18 6M6 6l12 12"
                              />
                            </svg>
                          </button>
                        </div>
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
          )}
          <div className="pb-8">
            <h3 className="pb-2 font-bold"></h3>
            <div>
              <button
                onClick={() => handleDelete(currentObjectId)}
                className="btn btn-outline btn-error btn-xs"
              >
                
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="h-4 w-4"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M6 18L18 6M6 6l12 12"
                  />
                </svg>
              </button>
            </div>
          </div>
        </div>
      }
    />
  );
}
